import { createReducer } from '@reduxjs/toolkit';
import { requestRates, requestRatesError, requestRatesSuccess } from './actions';
import { RateState } from '../../types/Rates';

const rateReducer = createReducer<RateState>({
  rates: null,
  loading: false,
  error: null,
}, {
  [requestRates.type]: (state) => ({
    ...state,
    rates: null,
    loading: true,
    error: null,
  }),
  [requestRatesSuccess.type]: (state, { payload }) => ({
    ...state,
    rates: payload,
    loading: false,
    error: null,
  }),
  [requestRatesError.type]: (state, { payload }) => ({
    ...state,
    rates: null,
    loading: false,
    error: payload,
  }),
});

export default rateReducer;
